.button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  // height: 50px;
  height: 70px;
  width: 100%;
  border: 1px solid $c-color-button;
  outline: none;
  background-color: #fff;
  cursor: pointer;
  transition: all 0.5s linear;

  &:hover {
    background-color: $c-color-button;
    box-shadow: 0 13px 27px rgba(13, 100, 17, 0.46);
    // transform: scale(1.2);
  }

  @include onTablet {
    display: inline-flex;
    height: 70px;
    width: 296px;
  }

  &__icon {
    display: inline-flex;
    height: 70px;
    width: 70px;
    // height: 50px;
    // width: 50px;
    background-color: $c-color-button;

    background-image: url(../images/arrow.png);

    @extend %bgr-image;

    // @include onTablet {
    //   height: 70px;
    //   width: 70px;
    // }
  }

  &__text {
    display: block;
    margin: 0 auto;
    padding: 0 80px;
    font-family: "IBM Plex Sans", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 100%;
    text-transform: uppercase;
  }
}
